<script>
import tableComponent from "@/components/Table/Table.vue";

//2024.04.18 新增搜尋功能
// 將搜尋內容轉小寫後，篩選符合結果
const toLower = (text) => {
  return text ? text.toString().toLowerCase() : text || "";
};
const searchByName = (items, term) => {
  if (term) {
    return items.filter((item) =>
      toLower(item.name_id).includes(toLower(term))
    );
  }
  return items;
};

export default {
  name: "companyProfileManagement-component",
  components: { tableComponent },
  data() {
    return {
      s_Id: "",
      search: "",
      category: "",
      selectedLists: [],
      data: [],
      tableData: [],
      tableList: [],
      currentPage: 1,
    };
  },
  computed: {},
  methods: {
    // 前端資料整理
    getNewData(data) {
      const newData = data.reduce((acc, cur) => {
        const newObj = {
          ...cur,
          ...{
            course_Id2: cur.course_Id === 37 ? 37 : null,
            course_Name2: cur.course_Id === 37 ? cur.course_Name : null,
            issue_Card2: cur.course_Id === 37 ? cur.issue_Card : null,
            she_Class_Date2: cur.course_Id === 37 ? cur.she_Class_Date : null,
          },
        };
        newObj.course_Id = cur.course_Id === 36 ? 36 : null;
        newObj.course_Name = cur.course_Id === 36 ? cur.course_Name : null;
        newObj.issue_Card = cur.course_Id === 36 ? cur.issue_Card : null;
        newObj.she_Class_Date =
          cur.course_Id === 36 ? cur.she_Class_Date : null;
        let index = acc.findIndex((v) => v.id === cur.id);
        if (index >= 0) {
          Object.keys(acc[index]).forEach((key) => {
            if (newObj[key]) {
              acc[index][key] = newObj[key];
            }
          });
        }
        if (index < 0) {
          acc.push(newObj);
        }
        return acc;
      }, []);
      return newData;
    },
    // elementUI Table 必要指定row-key
    getRowKeys(row) {
      if (row.index) {
        return row.index;
      } else {
        return row.id;
      }
    },
    goBack() {
      this.$router.go(-1);
    },
    // 篩選資料
    categoryHandler(val) {
      if (val !== "0") {
        this.tableData = this.data.filter((e) => e.course_Id === parseInt(val));
      } else {
        this.tableData = this.data;
      }
      this.tableList = this.tableData.slice(0, 10);
      this.currentPage = 1;
    },
    // table 全選事件
    changeSelection(item) {
      this.selectedLists = item;
    },
    changePage(page) {
      this.currentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
    },
    confirmHandler() {
      const s_id = this.selectedLists.find((e) => e.s_id).s_id;
      const emp_Ids = this.selectedLists.map((e) => e.id);
      const obj = {
        s_Id: s_id,
        emp_Ids: emp_Ids,
        course_Id: Number(this.category),
      };
      this.deleteCourse(obj);
    },
    async deleteCourse(obj) {
      try {
        const res = await this.$store.dispatch("deleteCourse", obj);
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "刪除成功",
          });
          this.$refs.multipleTable.clearSelection();
          this.getVdrEmpList(this.s_Id);
          this.currentPage = 1;
        }
      } catch (err) {
        console.log(err);
      }
    },
    // API取得資料
    async getVdrEmpList(s_id) {
      try {
        const res = await this.$store.dispatch("getVdrEmpList", s_id);
        let data = res.data.data;
        const newData = this.getNewData(data);
        this.data = JSON.parse(JSON.stringify(newData));
        this.tableData = this.data;
        this.tableData.forEach((e, index) => {
          e.index = index + 1;
          e.ID = e.id;
          e.name_id = `${e.p_Name ? e.p_Name : "無"}(${e.ID})`;
          e.contact = `${e.tel1}/${e.tel2}/${e.address}`;
          e.classDateS = e.she_Class_Date;
          e.makeCardS = e.issue_Card === 1 ? "已製卡" : "";
          e.insuranceDateS = `${e.insurance_Begin} ~ ${e.insurance_End}`;
          e.classDateN = e.she_Class_Date2;
          e.makeCardN = e.issue_Card2 === 1 ? "已製卡" : "";
          e.insuranceDateN = `${e.insurance_Begin} ~ ${e.insurance_End}`;
        });
        this.tableList = this.tableData.slice(0, 10);
      } catch (err) {
        console.log(err);
      }
    },
    //2024.04.18 新增搜尋功能
    async searchInput(evt) {
      if (evt.code === "Enter" || evt.code === "NumpadEnter") {
        const val = evt.target.value;
        this.tableData = searchByName(this.data, val);
        this.tableList = this.tableData.slice(0, 10);
        this.$store.state.currentPage = 1;
      }
    },
  },
  async mounted() {
    this.s_Id = this.$route.params.s_id;
    this.getVdrEmpList(this.s_Id);
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./staffManagement.html"></template>
<style lang="sass" scoped src="./staffManagement.sass"></style>
